import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';

function Formularios(
  { tipo,
    hospital,
    unidade,
    viewformulario,
    idatendimento,
    idusuario,
    usuario,
    firstname,
    lastname,
    funcao,
    conselho,
    box,
    nome,
    dn,
    idformulario,
    dataformulario,
    tipoformulario,
    textoformulario,
    statusformulario
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  // api para CID10:
  var cid10 = 'https://cid10-api.herokuapp.com/cid10';
  const changeState = useDispatch();

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewformulario);

  // lista de tipos de formulários.
  const [arrayformularios, setarrayformularios] = useState(
    [
      'ATESTADO MÉDICO',
      'DECLARAÇÃO DE COMPARECIMENTO',
      'RECEITA MÉDICA',
      'RELATÓRIO MÉDICO',
      'RELATÓRIO DE ALTA',
      'SUMÁRIO DE ÓBITO'
    ]);

  useEffect(() => {
    if (viewformulario === 1) { // inserir formulário.
      setshowtiposelector(0);
      setviewcomponent(1);
      setselecttipo('CLIQUE AQUI PARA ESCOLHER UM TIPO DE FORMULÁRIO');
    } else if (viewformulario === 2) { // atualizar formulário.
      setshowtiposelector(0);
      setviewcomponent(2);
      setselecttipo(tipoformulario);
    } else if (viewformulario === 3) { // visualizar formulário.
      setshowtiposelector(0);
      setviewcomponent(3);
      setselecttipo(tipoformulario);
    } else {
    }
  }, [viewformulario])

  // DECLARAÇÃO DE COMPARECIMENTO.
  const setDeclaracao = (valor) => {
    document.getElementById("inputTexto").value = "ATESTO, PARA OS DEVIDOS FINS, QUE O(A) PACIENTE "
      + nome + " ESTEVE EM ATENDIMENTO NESTE HOSPITAL, NO PERÍODO DE " + admissao + " A " + moment().format('DD/MM/YY') + "."
  }

  // ATESTADO.
  const setAtestado = (valor) => {
    document.getElementById("inputTexto").value = "ATESTO, PARA OS DEVIDOS FINS, QUE O(A) PACIENTE "
      + nome + " PRECISA AFASTAR-SE DO TRABALHO POR UM PERÍODO DE UM (01) DIA, A CONTAR DE "
      + moment().format('DD/MM/YY') + ", por motivo de doença CID 10 " + valor + "."
  }
  // exibindo tela para seleção de cid 10.
  const [showcidselector, setshowcidselector] = useState(0);
  function ShowCidSelector() {
    if (showcidselector === 1) {
      return (
        <div className="menucover" style={{ zIndex: 9, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div className="menucontainer">
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              DIAGNÓSTICO:
                </label>
            <input
              autoComplete="off"
              className="input"
              placeholder="DIAGNÓSTICO..."
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'DIAGNÓSTICO...')}
              onChange={() => filterDiagnostico()}
              title="DIAGNÓSTICO."
              style={{
                width: 0.3 * window.innerWidth,
              }}
              type="text"
              maxLength={200}
              id="inputDiagnostico"
            ></input>
            <div className="scroll"
              id="LISTA DE CIDs"
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                padding: 0,
                paddingRight: 10,
                width: 0.2 * window.innerWidth,
                height: 0.2 * window.innerHeight,
              }}
            >
              <div style={{ width: '100%' }}>
                {arraydiagnostico.map((item) => (
                  <button
                    className="blue-button"
                    title={item.nome.toUpperCase()}
                    style={{
                      width: '100%',
                      padding: 10,
                    }}
                    onClick={() => selectCid(item.codigo)}
                  >
                    {item.codigo}
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
  // gerando array de diagnósticos.
  const [listdiagnosticos, setlistdiagnosticos] = useState([]);
  const loadDiagnosticos = () => {
    axios.get(cid10).then((response) => {
      setlistdiagnosticos(response.data);
      setarraydiagnostico(response.data);
    });
  }
  // identificando o valor do cid.
  var timeout = null;
  var searchdiagnostico = '';
  const [arraydiagnostico, setarraydiagnostico] = useState([listdiagnosticos]);
  const filterDiagnostico = () => {
    clearTimeout(timeout);
    searchdiagnostico = document.getElementById("inputDiagnostico").value.toUpperCase();
    document.getElementById("inputDiagnostico").focus();
    timeout = setTimeout(() => {
      if (searchdiagnostico === '') {
        setarraydiagnostico([]);
        document.getElementById("inputDiagnostico").value = '';
        document.getElementById("inputDiagnostico").focus();
      } else {
        setarraydiagnostico(listdiagnosticos.filter(item => item.nome.toUpperCase().includes(searchdiagnostico) === true));
        document.getElementById("inputDiagnostico").value = searchdiagnostico;
        document.getElementById("inputDiagnostico").focus();
      }
    }, 3000);
  }
  // definindo o valor do cid.
  const [cid, setcid] = useState();
  const selectCid = (item) => {
    setcid(item);
    setshowcidselector(0);
    setAtestado(item);
  }

  // SUMÁRIO DE ALTA.
  // capturando as informações do atendimento (anamnese).
  const [admissao, setadmissao] = useState(0);
  const [antecedentes, setantecedentes] = useState(0);
  const [alergias, setalergias] = useState(0);
  const [medicacoes, setmedicacoes] = useState(0);
  const [examesprevios, setexamesprevios] = useState(0);
  const [historia, sethistoria] = useState(0);
  const loadAtendimento = () => {
    axios.get(html + "/atendimento/'" + hospital + "'/'" + unidade + "'/'"
      + idatendimento + "'").then((response) => {
        var x = [0, 1];
        x = response.data;
        setadmissao(x.map((item) => item.admissao));
        setantecedentes(x.map((item) => item.antecedentes));
        setalergias(x.map((item) => item.alergias));
        setmedicacoes(x.map((item) => item.medicacoes));
        setexamesprevios(x.map((item) => item.exames));
        sethistoria(x.map((item) => item.historia));
      });
  }
  // capturando resultados de exames de imagem referentes ao atendimento.
  const [listimagem, setlistimagem] = useState([]);
  const loadImagem = () => {
    setlistimagem([]);
    axios.get(html + "/image/'" + idatendimento + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      var y = [0, 1];
      y = x.filter((item) => item.status === 'PRONTO').map((item) => '» ' + item.exame + ' (' + (item.resultado).substring(0, 8) + '): ' + item.laudo).join('\n');
      setlistimagem(y.toString());
    });
  }
  // capturando informações do último exame físico.
  const [pas, setpas] = useState('');
  const [pad, setpad] = useState('');
  const [fc, setfc] = useState('');
  const [fr, setfr] = useState('');
  const [sao2, setsao2] = useState('');
  const [tax, settax] = useState('');
  const [acv, setacv] = useState('');
  const [ar, setar] = useState('');
  const [abdome, setabdome] = useState('');
  const [outros, setoutros] = useState('');
  const [glasgow, setglasgow] = useState('');
  const loadLastevolucao = () => {
    // ROTA: SELECT * FROM evolucao WHERE idatendimento = idatendimento.
    axios.get(html + '/lastevolucao/' + idatendimento).then((response) => {
      var x = [0, 1];
      x = response.data;
      setpas(x.map((item) => item.pas));
      setpad(x.map((item) => item.pad));
      setfc(x.map((item) => item.fc));
      setfr(x.map((item) => item.fr));
      setsao2(x.map((item) => item.sao2));
      settax(x.map((item) => item.tax));
      setacv(x.map((item) => item.acv));
      setar(x.map((item) => item.ap));
      setabdome(x.map((item) => item.abdome));
      setoutros(x.map((item) => item.outros));
      setglasgow(x.map((item) => item.glasgow));
    });
  }
  // capturando diagnósticos relacionados ao atendimento.
  const [diagnosticos, setdiagnosticos] = useState([]);
  const loadDiagnosticosAlta = () => {
    axios.get(html + "/diagnosticos/" + idatendimento).then((response) => {
      var x = [0, 1];
      x = response.data;
      setdiagnosticos(x.map((item) => item.cid + ' - ' + item.diagnostico + '.').join('\n').toString());
    });
  }

  // montando o texto predefinido do sumário de alta.
  const setAlta = (valor) => {
    document.getElementById("inputTexto").value =
      "DATA DE ADMISSÃO: " + admissao + " \n" +
      "ANTECEDENTES PESSOAIS: " + antecedentes + " \n" +
      "ALERGIAS: " + alergias + " \n" +
      "MEDICAÇÕES DE USO PRẼVIO: " + medicacoes + " \n" +
      "EXAMES PRÉVIOS RELEVANTES: " + examesprevios + " \n\n" +
      "CONTEXTO: " + historia + " \n\n" +
      "RESUMA AQUI OS PRINCIPAIS EVENTOS DA INTERNAÇÃO. \n\n" +
      "EXAMES RELEVANTES: \n" +
      listimagem + " \n\n" +
      "AO EXAME, GLASGOW " + glasgow + ", PA " + pas + ' x ' + pad + "MMHG, FC " + fc + " BPM, FR " + fr + " IRPM, " +
      "SAO2 " + sao2 + "%, TAX: " + tax + "°C. \n" +
      "ACV: " + acv + " \n" +
      "AP: " + ar + " \n" +
      "ABDOME: " + abdome + " \n" +
      "OUTROS: " + outros + " \n\n" +
      "DIAGNÓSTICOS: \n" +
      diagnosticos
  }

  // TRATAMENTO DOS REGISTROS DE FORMULÁRIOS.
  // inserindo registro.
  const insertData = () => {
    var conteudo = document.getElementById("inputTexto").value.toUpperCase()
    if (conteudo !== '' || selecttipo === '') {
      var obj = {
        idatendimento: idatendimento,
        data: moment().format('DD/MM/YY HH:mm'),
        tipo: selecttipo,
        texto: conteudo,
        idusuario: idusuario,
        usuario: usuario,
        status: 0,
      };
      axios.post(html + '/insertformulario', obj);
      toast(1, '#52be80', 'FORMULÁRIO REGISTRADO COM SUCESSO.', 6000);
      setTimeout(() => {
        fechar();
      }, 4000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // atualizando registro.
  const updateData = () => {
    var conteudo = document.getElementById("inputTexto").value.toUpperCase()
    if (conteudo !== '') {
      var obj = {
        idatendimento: idatendimento,
        data: dataformulario,
        tipo: selecttipo,
        texto: conteudo,
        idusuario: idusuario,
        usuario: usuario,
        status: 0,
      };
      axios.post(html + '/updateformulario/' + idformulario, obj);
      toast(1, '#52be80', 'FORMULÁRIO ATUALIZADO COM SUCESSO.', 6000);
      setTimeout(() => {
        fechar();
      }, 4000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // seleção do tipo de formulário.
  const [selecttipo, setselecttipo] = useState(tipoformulario);
  const selectTipo = (item) => {
    setselecttipo(item);
    setshowtiposelector(0);
    if (item === 'ATESTADO MÉDICO') {
      setshowcidselector(1);
      loadDiagnosticos();
    } else if (item === 'RELATÓRIO DE ALTA') {
      loadAtendimento();
      loadImagem();
      loadLastevolucao();
      loadDiagnosticosAlta();
      setTimeout(() => {
        setAlta();
      }, 2000);
    } else if (item === 'DECLARAÇÃO DE COMPARECIMENTO') {
      loadAtendimento();
      setTimeout(() => {
        setDeclaracao();
      }, 2000);
    } else {
    }
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  const fechar = () => {
    setviewcomponent(0);
    changeState({
      type: 'CORRIDA_OFF',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
  }

  // tela para seleção do tipo de formulário.
  const [showtiposelector, setshowtiposelector] = useState(0);
  function ShowTipoSelector() {
    if (showtiposelector === 1) {
      return (
        <div className="menucover" style={{ zIndex: 9, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div className="menucontainer">
            <div className="scroll"
              id="LISTA DE TIPOS DE FORMULÁRIO"
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                padding: 0,
                paddingRight: 10,
                width: 0.4 * window.innerWidth,
                height: 0.3 * window.innerHeight,
              }}
            >
              <div style={{ width: '100%' }}>
                {arrayformularios.map((item) => (
                  <button
                    className="blue-button"
                    style={{
                      width: '100%',
                      padding: 10,
                    }}
                    onClick={() => selectTipo(item)}
                  >
                    {item}
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <ShowTipoSelector></ShowTipoSelector>
        <ShowCidSelector></ShowCidSelector>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            CRIAR FORMULÁRIO
          </label>
          <div
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              width: 0.7 * window.innerWidth,
              margin: 0,
              padding: 5,
            }}
          >
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '100%' }}>
              <button className="blue-button" style={{ width: '99%', margin: 5, alignSelf: 'center' }} onClick={() => setshowtiposelector(1)}>
                {selecttipo}
              </button>
              <textarea
                autoComplete="off"
                className="textarea"
                placeholder=""
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = '')}
                style={{
                  width: '99%',
                  minWidth: '99%',
                  height: 300,
                  margin: 5,
                  alignSelf: 'center',
                }}
                type="text"
                maxLength={1000}
                id="inputTexto"
              >
              </textarea>
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 30,
              marginBottom: 0,
            }}
          >
            <button className="green-button" onClick={() => insertData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="red-button" onClick={() => fechar()}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 2) { // atualizar.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <ShowTipoSelector></ShowTipoSelector>
        <ShowCidSelector></ShowCidSelector>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            EDITAR FORMULÁRIO
        </label>
          <div
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              width: 0.7 * window.innerWidth,
              margin: 0,
              padding: 5,
            }}
          >
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '100%' }}>
              <button className="blue-button" style={{ width: '99%', margin: 5, alignSelf: 'center' }} onClick={() => setshowtiposelector(1)}>
                {selecttipo}
              </button>
              <textarea
                autoComplete="off"
                className="textarea"
                placeholder=""
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = '')}
                defaultValue={textoformulario}
                style={{
                  width: '99%',
                  minWidth: '99%',
                  height: 300,
                  margin: 5,
                  alignSelf: 'center',
                }}
                type="text"
                maxLength={1000}
                id="inputTexto"
              >
              </textarea>
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 30,
              marginBottom: 0,
            }}
          >
            <button className="green-button" onClick={() => updateData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="red-button" onClick={() => fechar()}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 3) { // visualizar.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            VISUALIZAR FORMULÁRIO
          </label>
          <div
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              width: 0.7 * window.innerWidth,
              margin: 0,
              padding: 5,
            }}
          >
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '100%' }}>
              <button
                className="blue-button"
                style={{ width: '99%', margin: 5, alignSelf: 'center', backgroundColor: '#1f7a8c' }}
                disabled={true}
              >
                {selecttipo}
              </button>
              <textarea
                disabled={true}
                autoComplete="off"
                className="textarea"
                placeholder=""
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = '')}
                defaultValue={textoformulario}
                style={{
                  width: '99%',
                  minWidth: '99%',
                  height: 300,
                  margin: 5,
                  alignSelf: 'center',
                }}
                type="text"
                maxLength={1000}
                id="inputTexto"
              >
              </textarea>
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 30,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => fechar()}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Formularios;
