import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import moment, { locale } from 'moment';
import { Page, Text, View, Document, StyleSheet, PDFViewer } from '@react-pdf/renderer';
import html2canvas from 'html2canvas';
import { Font } from '@react-pdf/renderer';
import fontbold from '../fonts/Roboto-Bold.ttf';

function PrintEvolucao(
  { tipo,
    hospital,
    unidade,
    viewprintevolucao,
    idevolucao,
    data,
    idatendimento,
    idusuario,
    usuario,
    firstname,
    lastname,
    funcao,
    conselho,
    idusuariologado,
    box,
    nome,
    dn,
    evolucao,
    pas,
    pad,
    fc,
    fr,
    sao2,
    tax,
    diu,
    fezes,
    bh,
    acv,
    ar,
    abdome,
    outros,
    glasgow,
    rass,
    ramsay,
    hd,
    uf,
    heparina,
    braden,
    morse,
  }) {

  const [viewcomponent, setviewcomponent] = useState(viewprintevolucao);
  useEffect(() => {
    setviewcomponent(viewprintevolucao);
    if (viewcomponent === 1) {
    }
  }, [viewprintevolucao])

  const changeState = useDispatch();
  const fechar = () => {
    setviewcomponent(0);
    setshowpdf(0);
  }

  // componente PDF.
  function PageBackup() {
    return (
      <div
        id="canvas"
        style={{
          width: window.innerWidth,
          height: window.innerHeight,
          margin: 30,
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          alignSelf: 'center',
          fontWeight: 'bold',
        }}>
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          width: 600,
          alignItems: 'center',
          alignSelf: 'center'
        }}>
          <div>
            <div className="pdf1" style={{ backgroundColor: '#EAECEE', fontSize: 16, width: 250 }}>{'ATENDIMENTO: ' + idatendimento}</div>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly' }}>
              <div style={{ fontSize: 18 }}>{'BOX: ' + box}</div>
              <div style={{ fontSize: 18 }}>{'NOME: ' + nome}</div>
              <div style={{ fontSize: 18 }}>{'IDADE: ' + moment().diff(moment(dn, 'DD/MM/YYYY'), 'years')}</div>
              <div>{'DN: ' + dn}</div>
            </div>
          </div>
          <div>
            <div style={{ fontSize: 22, margin: 10, padding: 5 }}>
              {funcao === 'MED' ? 'EVOLUÇÃO MÉDICA' : funcao === 'ENF' ? 'EVOLUÇÃO DE ENFERMAGEM' : 'EVOLUÇÃO MULTIPROFFISIONAL'}
            </div>
          </div>
          <div id="CONTEÚDO" className="pdf2" style={{ flexDirection: 'column' }}>
            <div style={{ textAlign: 'justify', margin: 5, padding: 5 }}>{evolucao}</div>
            <div style={{ textAlign: 'justify', textDecoration: 'underline', marginTop: 10, marginBottom: 0, padding: 5 }}>ESCALAS:</div>
            <div style={{ display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'space-between', width: '80%' }}>
              <div className="pdf3" style={{ display: glasgow !== '' ? 'flex' : 'none', width: 150 }}>{'GLASGOW: ' + glasgow}</div>
              <div className="pdf3" style={{ display: glasgow !== '' ? 'flex' : 'none', width: 150 }}>{'RAMSAY: ' + ramsay}</div>
              <div className="pdf3" style={{ display: glasgow !== '' ? 'flex' : 'none', width: 150 }}>{'RASS: ' + rass}</div>
            </div>
            <div style={{ display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'space-evenly' }}>
              <div className="pdf3" style={{ width: 150 }}>{'BRADEN: ' + braden}</div>
              <div className="pdf3" style={{ width: 150 }}>{'MORSE: ' + morse}</div>
            </div>
            <div style={{ textAlign: 'justify', textDecoration: 'underline', marginTop: 10, marginBottom: 0, padding: 5 }}>DADOS CLÍNICOS:</div>
            <div className="pdf3" style={{ display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'column', width: '100%' }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', margin: 5, width: '95%' }}>
                <div>{'PA: ' + pas + '/' + pad + ' mmHg'}</div>
                <div>{'FC: ' + fc + ' BPM'}</div>
                <div>{'FR: ' + fr + ' IRPM'}</div>
                <div>{'SAO2: ' + sao2 + '%'}</div>
                <div>{'TAX: ' + tax + '°C'}</div>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', margin: 5, width: '95%' }}>
                <div>{'EVACUAÇÕES: ' + fezes}</div>
                <div>{'DIURESE: ' + diu + 'ML/12H'}</div>
                <div>{'BH: ' + bh + 'ML/12H'}</div>
              </div>
            </div>
            <div style={{ display: (funcao === 'MED' || funcao === 'ENF') && hd === 1 ? 'flex' : 'none', textAlign: 'justify', textDecoration: 'underline', marginTop: 10, marginBottom: 0, padding: 5 }}>HEMODIÁLISE:</div>
            <div style={{ display: (funcao === 'MED' || funcao === 'ENF') && hd === 1 ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'center', width: '60%' }}>
              <div className="pdf3" style={{ width: 150 }}>{'UF: ' + uf + 'ML'}</div>
              <div className="pdf3" style={{ width: 150 }}>{'HEPARINA: ' + heparina === 1 ? 'HEPARINA: SIM' : 'HEPARINA: NÃO'}</div>
            </div>
            <div style={{ display: (funcao === 'MED' || funcao === 'ENF') && hd === 1 ? 'flex' : 'none', textAlign: 'justify', textDecoration: 'underline', marginTop: 10, marginBottom: 0, padding: 5 }}>EXAME FÍSICO:</div>
            <div style={{ display: funcao === 'MED' ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'center', width: '95%' }}>
              <div className="pdf3" style={{ flexDirection: 'column', justifyContent: 'flex-start', width: 250, height: 100, textAlign: 'left' }}>
                {acv !== '' ? 'APARELHO CARDIOVASCULAR: ' + acv : 'APARELHO CARDIOVASCULAR: NÃO PREENCHIDO.'}
              </div>
              <div className="pdf3" style={{ flexDirection: 'column', justifyContent: 'flex-start', width: 250, height: 100, textAlign: 'left' }}>
                {ar !== '' ? 'APARELHO RESPIRATÓRIO: ' + ar : 'APARELHO RESPIRATÓRIO: NÃO PREENCHIDO.'}
              </div>
            </div>
            <div style={{ display: funcao === 'MED' ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'center', width: '95%' }}>
              <div className="pdf3" style={{ flexDirection: 'column', justifyContent: 'flex-start', width: 250, height: 100, textAlign: 'left' }}>
                {abdome !== '' ? 'ABDOME: ' + abdome : 'ABDOME: NÃO PREENCHIDO.'}
              </div>
              <div className="pdf3" style={{ flexDirection: 'column', justifyContent: 'flex-start', width: 250, height: 100, textAlign: 'left' }}>
                {outros !== '' ? 'OUTROS: ' + outros : 'OUTROS: SEM OUTROS ACHADOS AO EXAME FÍSICO.'}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // registro de fontes para o react-pdf (a lib não aceita ajustar fontWeight).
  Font.register({
    family: 'Roboto',
    src: fontbold,
  });

  function Pdf() {
    return (
      <PDFViewer style={{ width: window.innerWidth, height: window.innerHeight, fontSize: 10 }}>
        <Document>
          <Page size="A4">
            <View style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-start' }}>
              <View style={{ margin: 20 }}>
                <View id="CABEÇALHO" style={{
                  display: 'flex', flexDirection: 'column', justifyContent: 'center', width: '100%', borderRadius: 5, padding: 10,
                  borderColor: '#000000',
                  borderWidth: 1,
                  borderRadius: 5,
                }}>
                  <View style={{ flexDirection: 'row', justifyContent: 'space-between', margin: 2.5 }}>
                    <Text style={{ fontSize: 10, margin: 0, padding: 5, backgroundColor: '#D5DBDB', borderRadius: 5 }}>{'ATENDIMENTO: ' + idatendimento}</Text>
                    <Text style={{ fontSize: 10, margin: 0, padding: 5 }}>{'SETOR: ' + unidade + ' - BOX: ' + box}</Text>
                  </View>
                  <Text style={{ fontSize: 12, margin: 5, fontWeight: 'bold' }}>{'PACIENTE: ' + nome}</Text>
                  <View style={{ flexDirection: 'row' }}>
                    <Text style={{ fontSize: 10, margin: 5 }}>{'DN: ' + dn}</Text>
                    <Text style={{ fontSize: 10, margin: 5 }}>{'(' + moment().diff(moment(dn, 'DD/MM/YYYY'), 'years') + ' ANOS)'}</Text>
                  </View>
                  <Text style={{ fontSize: 10, margin: 5 }}>{'RESPONSÁVEL PELA EVOLUÇÃO: ' + usuario + ' - ' + conselho}</Text>
                  <Text style={{ fontSize: 10, margin: 5 }}>{'DATA DA EVOLUÇÃO: ' + data}</Text>
                </View>
                <View style={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'flex-start',
                  width: '100%',
                  //borderColor: '#000000',
                  //borderWidth: 1,
                  //borderRadius: 5,
                  marginTop: 5,
                  height: '78vh',
                }}>
                  <Text style={{ fontSize: 18, margin: 10, padding: 5, alignSelf: 'center' }}>
                    {funcao === 'MED' ? 'EVOLUÇÃO MÉDICA' : funcao === 'ENF' ? 'EVOLUÇÃO DE ENFERMAGEM' : 'EVOLUÇÃO MULTIPROFFISIONAL'}
                  </Text>
                  <View id="CONTEÚDO" style={{ flexDirection: 'column', justifyContent: 'center', fontSize: 10, width: '100%' }}>
                    <View style={{
                      display: 'flex', flexDirection: 'column', justifyContent: 'flex-start',
                      backgroundColor: '#EAEDED', borderRadius: 5, textAlign: 'left', margin: 0, padding: 5, minHeight: 50, maxHeight: 100
                    }}>
                      <Text style={{ padding: 2.5 }}>{evolucao}</Text>
                      <Text style={{
                        display: hd === 1 ? 'flex' : 'none', padding: 2.5,
                        backgroundColor: "#D6DBDF", borderRadius: 5, padding: 5, width: 230
                      }}>
                        {heparina === 1 ? 'REALIZADA HD, UF: ' + uf + 'ML,  COM HEPARINA' : 'REALIZADA HD, UF: ' + uf + 'ML,  SEM HEPARINA'}</Text>
                    </View>
                    <Text style={{
                      textAlign: 'justify', textDecoration: 'underline', alignSelf: 'center',
                      margin: 5, marginTop: 15
                    }}>
                      ESCALAS:
                    </Text>
                    <View style={{
                      display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'row',
                      justifyContent: 'space-between', margin: 0,
                    }}>
                      <Text
                        style={{
                          backgroundColor: '#EAEDED',
                          display: glasgow !== '' ? 'flex' : 'none', width: 80,
                          textAlign: 'center',
                          borderRadius: 5, padding: 5, margin: 2.5
                        }}>
                        {'GLASGOW: ' + glasgow}</Text>
                      <Text
                        style={{
                          display: glasgow !== '' ? 'flex' : 'none', width: 80,
                          backgroundColor: '#EAEDED',
                          textAlign: 'center',
                          borderRadius: 5, padding: 5, margin: 2.5
                        }}>
                        {'RAMSAY: ' + ramsay}</Text>
                      <Text
                        style={{
                          display: glasgow !== '' ? 'flex' : 'none', width: 80,
                          backgroundColor: '#EAEDED',
                          textAlign: 'center',
                          borderRadius: 5, padding: 5, margin: 2.5
                        }}>
                        {'RASS: ' + rass}</Text>
                      <Text
                        style={{
                          width: 80, backgroundColor: '#EAEDED',
                          textAlign: 'center',
                          borderRadius: 5, padding: 5, margin: 2.5
                        }}>{'BRADEN: ' + braden}
                      </Text>
                      <Text
                        style={{
                          width: 80, backgroundColor: '#EAEDED',
                          textAlign: 'center',
                          borderRadius: 5, padding: 5, margin: 2.5
                        }}>{'MORSE: ' + morse}
                      </Text>
                    </View>
                    <Text style={{ textAlign: 'justify', textDecoration: 'underline', margin: 0, marginTop: 10, marginBottom: 5, alignSelf: 'center' }}>DADOS CLÍNICOS:</Text>
                    <View style={{ display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'column', width: '100%' }}>
                      <View style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', margin: 0, width: '100%' }}>
                        <Text style={{ width: 100, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'PA: ' + pas + '/' + pad + ' mmHg'}</Text>
                        <Text style={{ width: 100, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'FC: ' + fc + ' BPM'}</Text>
                        <Text style={{ width: 100, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'FR: ' + fr + ' IRPM'}</Text>
                        <Text style={{ width: 100, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'SAO2: ' + sao2 + '%'}</Text>
                        <Text style={{ width: 100, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'TAX: ' + tax + '°C'}</Text>
                      </View>
                      <View style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', margin: 0, width: '100%' }}>
                        <Text style={{ width: 170, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'EVACUAÇÕES: ' + fezes}</Text>
                        <Text style={{ width: 170, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'DIURESE: ' + diu + 'ML/12H'}</Text>
                        <Text style={{ width: 170, textAlign: 'center', backgroundColor: '#EAEDED', borderRadius: 5, padding: 5, margin: 2.5 }}>{'BH: ' + bh + 'ML/12H'}</Text>
                      </View>
                    </View>
                    <Text style={{ display: funcao === 'MED' ? 'flex' : 'none', textAlign: 'center', textDecoration: 'underline', margin: 0, marginTop: 5, padding: 5 }}>EXAME FÍSICO:</Text>
                    <View style={{
                      display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'row',
                      justifyContent: 'space-between', margin: 0,
                    }}>
                      <Text
                        style={{
                          flexDirection: 'column',
                          justifyContent: 'flex-start',
                          width: 272.5,
                          height: 100,
                          textAlign: 'left',
                          padding: 5,
                          margin: 2.5,
                          borderRadius: 5,
                          backgroundColor: '#EAEDED'
                        }}>
                        {acv !== '' ? 'APARELHO CARDIOVASCULAR: ' + acv : 'APARELHO CARDIOVASCULAR: -x-'}
                      </Text>
                      <Text
                        style={{
                          flexDirection: 'column',
                          justifyContent: 'flex-start',
                          width: 272.5,
                          height: 100,
                          textAlign: 'left',
                          padding: 5,
                          margin: 2.5,
                          borderRadius: 5,
                          backgroundColor: '#EAEDED'
                        }}>
                        {ar !== '' ? 'APARELHO RESPIRATÓRIO: ' + ar : 'APARELHO RESPIRATÓRIO: -x-'}
                      </Text>
                    </View>
                    <View style={{
                      display: funcao === 'MED' || funcao === 'ENF' ? 'flex' : 'none', flexDirection: 'row',
                      justifyContent: 'space-between', margin: 0,
                    }}>
                      <Text
                        style={{
                          flexDirection: 'column',
                          justifyContent: 'flex-start',
                          width: 272.5,
                          height: 100,
                          textAlign: 'left',
                          padding: 5,
                          margin: 2.5,
                          borderRadius: 5,
                          backgroundColor: '#EAEDED'
                        }}>
                        {abdome !== '' ? 'ABDOME: ' + abdome : 'ABDOME: -x-'}
                      </Text>
                      <Text
                        style={{
                          flexDirection: 'column',
                          justifyContent: 'flex-start',
                          width: 272.5,
                          height: 100,
                          textAlign: 'left',
                          padding: 5,
                          margin: 2.5,
                          borderRadius: 5,
                          backgroundColor: '#EAEDED'
                        }}>
                        {outros !== '' ? 'OUTROS: ' + outros : 'OUTROS: -x-'}
                      </Text>
                    </View>
                  </View>
                  <View style={{ fontSize: 10, display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'center', width: '100%', marginBottom: 5 }}>
                    <Text style={{ marginTop: 20 }}> _______________________________ </Text>
                    <Text>{firstname + ' ' + lastname}</Text>
                    <Text>{conselho}</Text>
                  </View>
                </View>
              </View>
            </View>
          </Page>
        </Document>
      </PDFViewer >
    )
  }

  function BackButton() {
    return (
      <div style={{ position: 'absolute', left: 20, bottom: 20, zIndex: 21 }}>
        <button
          className="green-button"
          onClick={() => fechar()}
          style={{
            width: 120,
            height: 50,
            margin: 2.5,
            padding: 5,
          }}
        >
          VOLTAR
      </button>
      </div>
    );
  }

  // função inativa, para impressão de canvas ao invés de pdf.
  const [showpdf, setshowpdf] = useState(0);
  const [pdf, setpdf] = useState();
  const imprimir = () => {
    html2canvas(document.querySelector("#canvas")).then(canvas => {
      //window.open().document.write('<img src="' + canvas.toDataURL() + '" />');
      setpdf(canvas);
      setshowpdf(1);
    });
  }

  // renderização do componente.
  if (viewcomponent === 1) {
    return (
      <div
        className="menucover"
        id="printevolucao"
        style={{
          flexDirection: 'column',
          justifyContent: 'flex-start',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          backgroundColor: '#ffffff',
          borderColor: 'transparent',
          padding: 0,
          borderWidth: 0,
          borderRadius: 0,
          zIndex: 900,
        }}>
        <Pdf></Pdf>
        <BackButton></BackButton>
      </div>
    )
  } else {
    return null
  }
}

export default PrintEvolucao;